<template>
  <main class="contact container">
    <section class="container-body">
      <div class="contact-container">
        <h1 class="text-center" tabindex="0">Contact REH Designs</h1>
        <p tabindex="0">Please message me to schedule a consultation or ask any questions you may have about your website needs. I'd love to hear from you!</p>
        <section class="alert alert-danger" role="alert" aria-describedby="errorMessage" v-show="Object.keys(errors).length !== 0">
          <div id="errorMessage">
            <h2 class="h5">Please fix the following {{Object.keys(errors).length}} error(s):</h2>
            <ul>
              <li v-show="errors.firstName" id="firstname_error" class="form-error">
                <a href="#firstName">
                  First Name is required
                </a>
              </li>
              <li v-show="errors.lastName" id="lastname_error" class="form-error">
                <a href="#lastName">
                  Last Name is required
                </a>
              </li>     
              <li v-show="errors.email" id="email_error" class="form-error">
                <a href="#email">
                  Email is required
                </a>
              </li>
              <li v-show="errors.questions" id="questions_error" class="form-error">
                <a href="#questions">
                  Questions &amp; Comments are required
                </a>
              </li>
            </ul>
          </div>
        </section>
        <form action="https://docs.google.com/forms/d/e/1FAIpQLScBUGrAHUDBk-7I05p2xIPikdCOclfivSSRkCH3a1KxZaJKuQ/formResponse" 
          @submit="checkForm" 
          method="post" 
          target="_blank">
          <div class="form-group">
            <label for="firstName">First Name</label>
            <input type="text" class="form-control" name="entry.1252483588" id="firstName" v-model="firstName" aria-describedby="firstname_error">
          </div>
          <div class="form-group">
            <label for="lastName">Last Name</label>
            <input type="text" class="form-control" name="entry.1700436106" id="lastName" v-model="lastName" aria-describedby="lastname_error">
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <input type="email" class="form-control" name="entry.872689138" id="email" v-model="email" aria-describedby="email_error">
          </div>
          <div class="form-group">
            <label for="questions">Questions &amp; Comments</label>
            <textarea type="text" class="form-control" name="entry.776215335" id="questions" v-model="questions" aria-describedby="questions_error"></textarea>        
          </div>
          <div class="text-right">
            <input class="btn btn-secondary" type="submit" value="Submit">
          </div>
        </form>
      </div>                      
    </section>
  </main>
</template>


<script>

export default {
  name: 'Contact',
  data () {
    return {
      errors: {},
      firstName: null,
      lastName: null,
      email: null,
      questions: null
    }
  },
  methods: {
    checkForm: function (e) {
      this.errors = {};
      const firstNameIsValid = this.fieldIsValid(this.firstName)
      const lastNameIsValid = this.fieldIsValid(this.lastName)
      const emailIsValid = this.fieldIsValid(this.email)
      const questionsIsValid = this.fieldIsValid(this.questions)
      if (firstNameIsValid && lastNameIsValid && emailIsValid && questionsIsValid) {
        return true;
      }

      if (!firstNameIsValid) {
        this.errors.firstName = true;
      }
      if (!lastNameIsValid) {
        this.errors.lastName = true;
      }
      if (!emailIsValid) {
        this.errors.email = true;
      }
      if (!questionsIsValid) {
        this.errors.questions = true;
      }      

      e.preventDefault();
    },
    fieldIsValid (value) {
      return value && value.trim().length !== 0
    }
  }

}
</script>

<style scoped lang="less">
@import '~../less/variables.less';
@import '~../less/global.less';

.container-body{
  min-height: 560px;
}

.alert{
  .h5{
    font-family: 'Quicksand', sans-serif;
  }

  ul{
    padding-left: 15px;
  }

  .form-error{
    padding: 2px 0px;
    a{
      color: darken(@red, 15%);
      font-size: 14px;

      &:hover,
      &:active{
        color: @red;
      }
    }
  }
}

.contact-container{
  max-width: 500px;
  margin: 0px auto;
}

label{
  font-weight: bold;
  margin-bottom: 2px;
}

.form-error{
  color: @red;
  font-size: 12px;
  font-weight: bold;
}
</style>